.color-area {
  display: flex;
  width: 21px;
  height: 21px;
  margin-right: 3px;
  border: #f5f5f5 0 solid;
  border-radius: 20px;
  color: white;
  align-items: center;
  justify-content: space-around;


  span {
    mix-blend-mode: difference;
  }
}

.ant-popover-inner-content {
  padding: 0 !important;

  .color-picker {
    padding: 5px;
  }
}